*{
  margin: 0;
  padding: 0;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
}
html, body {
  background-color: #000;
  color: #969491;
}
input[type="submit"], button{
  background-color: #bab9b9;
  padding: 5px;
  text-transform: capitalize;
  font-weight: bold;
}
input:hover, select:hover, button:hover{
  background-color: #e2e1e1;
  transition: all .4s ease;
}
input, select {
  background-color: #bab9b9;
}
.custom-grid-84{
  padding: 10px;
  display: grid;
  grid-template-columns: 350px auto;
}
.custom-grid{
  padding: 10px;
  display: grid;
  grid-template-columns: auto auto;
  gap: 10px;
}
.custom-grid-list {
  margin: 0;
  padding: 0;
  list-style: none;
}
.custom-grid-list li{
  margin-right: 10px;
  display: inline-block;
}
.custom-grid-list li:last-child{
  margin-right: 0;
}
.table{
  width: 100%;
  text-align: left;
}